.col_artimain{
    display: flex;
    justify-content: space-between;
}

.col_artimain .single-article {
    display: flex;
      padding: 2%;
      background: #fff;
      padding-bottom: 6.25%;
      margin-bottom: 4%;
  }
  .col_artimain .single-article:hover {
    box-shadow: 6px 5px 10px -5px;
  }
  .col_artimain .single-article .left {
    width: 44%;
  }
  .col_artimain .single-article .left a {
    width: 100%;
      display: flex;
  }
  .col_artimain .single-article .left a img {
    width: 100%;
  }
  .col_artimain .single-article .right {
    padding: 0 4%;
    width: 56%;
  }
  .col_artimain .single-article .right span {
    color: #333;
    font-size: 0.8vw;
    line-height: 0.8vw;
  }
  .col_artimain .single-article .right h2 {
    margin-top: 10px;
      margin-bottom: 3%;
      line-height: 1.6;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 24px;
  }
  .col_artimain .single-article .right h2 a {
    text-decoration: none;
      color: #000;
  }
  .col_artimain .single-article .right p {
    font-size: 16px;
    margin-top: 10px;
    line-height: 1.6;
    color: #333;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .col_artimain .single-article .right .links {
    display: flex;
      justify-content: center;
      position: relative;
      margin-top: 4%;
  }
  .col_artimain .single-article .right .links::before,
  .col_artimain .single-article .right .links::after {
    content: "";
      width: 40%;
      height: 2px;
      background: #333;
      position: absolute;
      top: 45%;
  }
  .col_artimain .single-article .right .links::before {
    left: 0;
  }
  .col_artimain .single-article .right .links::after {
    right: 0;
  }
  .col_artimain .single-article .right .links a {
    margin: 2%;
  }
  .col_artimain .single-article .right .links svg {
    fill: #333;
    font-size: 1vw;
  }
  .col_artimain .single-article .right .links a:hover svg {
    fill: #60c2f1;
    scale: 1.5;
  }