@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


a{
  text-decoration: none;
}

@keyframes lightSweep {
  0% {
    right: 3px;
  }
  30% {
    right: -10%;
  }
  100% {
    right: 3px;
  }
}
@keyframes lightsweeping {
  
  0% {
    background-position: 200% 0; /* Move to the left side */
  }
  20% {
    background-position: 100% 0; /* Move to the left side */
  }
  100% {
    background-position: -200% 0; /* Move to the left side */
  }
}

*{
  font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: white;
}

::-webkit-scrollbar-thumb {
  background: #356aae;
  border-radius: 0;
  border-left: 1px solid #fff;
}
*{
  scrollbar-width: thin;
  scrollbar-color: blue white;
}

html {
  scroll-behavior: smooth;
}

.section {
  position: relative;
  background-color: #fff;
  background-position: 50%;
  background-size: 100%;
  background-size: cover;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.content_starts {
  width: 95% !important;
  max-width: 1170px !important;
  display: flex;
  flex-wrap: wrap;
}

.sectionfull .content_starts {
  width: 100% !important;
  max-width: 100% !important;
}

.wmain {
  align-items: start;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.w20 {
  width: 20%;
}

.w25 {
  width: 25%;
}

.w33 {
  width: 33%;
}

.w40 {
  width: 40%;
}
.w50 {
  width: 50%;
}

.w60 {
  width: 60%;
}
.w70 {
  width: 70%;
}
.w80 {
  width: 80%;
}

.w100 {
  width: 100%;
}




.header {
  width: 100%;
  height: fit-content;
  position: fixed;
  top: 0;
  margin: 0;
  padding: 0;
  z-index: 100; 
  box-shadow: 0px 5px 30px -15px;
}

.header .social-cont {
  display: flex;
  align-items: center;
  justify-content: end;
  padding-top: 8px;
  font-size: 30px;
}
.header .social-cont a{
margin-left: 1.25vw;
}
.header .social-cont .form{
position: relative;
}
.header .social-cont .form input{
height: 2vw;
  width: 25vw;
  border-radius: 1vw;
  border: 2px solid #838383;
  display: none;
  font-size: 1vw;
  padding-left: 1vw;
  color: #356aae;
}
.header .social-cont .form input:hover{
  border: 2px solid #356aae;
}
.header .social-cont .form a{
font-size: 1.1vw;
  width: 100%;
  height: 100%;
  display: block;
  margin-right: 0.6vw;
}
.header .social-cont .form a[id="srch"]{
position: absolute;
  right: 0;
  bottom: 20%;
  width: fit-content;
  height: fit-content;
  display: none;
}
.header .social-cont .form a[id="srch"]:hover{
fill: #356aae;
}

.header .nav-cont {
  width: 100%;
    height: fit-content;
    display: flex;
    justify-content: space-between;
    align-items: end;
    background: #fff;
    padding-bottom: 10px;
    padding-top: 25px;
}
.header .nav-cont span{
font-size: 35px;
  color: #356aae;
  font-weight: 500;
}

.header .nav-cont ul{
display: flex;
list-style: none;
justify-content: space-between;
margin: 0;
    gap: 20px;
}
.header .nav-cont ul li a{
text-decoration: none;
color: #000000;
  font-weight: 500;
  font-size: 16px;
}
.header .nav-cont ul li a:hover{
color: #356aae;
}

svg[id="search"]{
  fill: #838383;
}

svg[id="twitter"]{fill:#60c2f1}

svg[id="linkedin"]{fill:#3375b0}

.footer {
  background-image: linear-gradient(to bottom , #2b4b98, #356aae);
  color: #fff;
}
 .foot-cont{
  display: flex;
  padding-top: 5%;
}
 .foot-cont .links{
  width: 36.5%;
}
 .foot-cont .links ul{
  list-style: none;
    margin-top: 30px;
    padding: 0;
    gap: 15px;
    display: grid;
}
 .foot-cont .links ul li{
  display: flex;
}
 .foot-cont .links ul li svg{
  fill: #fff;
    font-size: 1.5vw;
}
 .foot-cont .links ul li a{
  text-decoration: none;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    margin-left: 15px;
}
 .foot-cont span{
  color: #fff;
    border-bottom: 2px solid #fff;
    font-size: 30px;
}
 .foot-cont .contact{
  width: 45.5%;
}
 .foot-cont .contact h1{
  margin-top: 30px;
}
 .foot-cont .contact a{
  text-decoration: none;
    color: #fff;
    font-size: 1.3vw;
}
 .foot-cont .more{
  width: 18%;
}
 .foot-cont .more ul{
  display: flex;
    margin-top: 30px;
    padding: 0;
    gap: 15px;
}
 .foot-cont .more ul div{
  background: #fff;
  border-radius: 50%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 10px solid #fff;
}
 .foot-cont .more ul div svg{
  font-size: 2vw;
}

 .copy{
  display: flex;
  padding: 3%;
  justify-content: center;
    font-weight: 300;
}

.carousel {
  width: 100%;
  height: auto;
  overflow: hidden;
  position: relative;
  margin-top: 100px;
}

.carousel ul {
  list-style: none;
  width: 300%;
  height: 100%;
  padding: 0;
  margin: 0;
  position: relative;
  transition: left 0.8s ease-in-out;
}

.carousel li {
  width: 33.33%;
  height: 100%;
  float: left;
}

.carousel img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.carousel progress{
width: 30px;
height: 8px;
margin-left: 5px;
-webkit-appearance: none;
position: relative;
}

.carousel progress::after{
  content: "";
    position: absolute;
    background: #7e8dac;
    top: 0;
    left: 0;
    width: 30px;
    height: 8px;
    border-radius: 4px;
    opacity: 1;
    z-index: 2;
  }
  .carousel progress.active::after{
      opacity: 0;
    }


.carousel progress::-webkit-progress-bar{
background-color: #465676;
border-radius: 1vh;
}
.carousel progress::-webkit-progress-value{
background-color: #7e8dac;
border-radius: 1vh;
}
.carousel .pointers{
width: 100%;
height: 4vh;
position: absolute;
display: flex;
justify-content: center;
bottom: 1vh;
align-items: center;
}
.carousel .pointers a{
height: 100%;
  aspect-ratio: 1 / 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.2);
  font-size: 2vh;
  color: rgb(255, 255, 255);
  border-radius: 50%;
  margin-left: 1vh;
  padding: 2vh;
  cursor: pointer;
}


.section.port{
  background-image: linear-gradient(to bottom , #2b4b98, #356aae);
}

.col_port .w25{
  flex-direction: column;
  padding: 40px;
}
.col_port h1{
  font-size: 50px;
  font-weight: 500;
  position: relative;
  background: linear-gradient(to top right, #000 5%, #fff 60%);
  background-size: 200% 100%;
  color: transparent;
  -webkit-background-clip: text;
  animation: lightsweeping 2s linear infinite;
}
.col_port span{
  color: #fff;
    font-size: 100%;
    font-weight: 300;
}

.posts-container, .g-container {
  width: 100%;
    overflow: hidden;
    padding: 0;
    margin-top: 1rem;
}

.posts-slide, .g-slide {
  display: flex;
  transition: transform 0.5s ease-in-out;
  margin: 0;
  padding: 0;
}

.article_card, .g_card {
  flex: 0 0 25%;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.article_card img, .g_card img {
  width: 92%;
  aspect-ratio: 3/2;
  margin: 0;
  padding: 0;
}
.article_card h4{
  margin-bottom: 15px;
    font-weight: 300;
    font-size: 18px;
    width: 92%;
}
.article_card .content{
  background: #f4f7fe;
  border-bottom: 3px solid #356aae;
  width: 92%;
}
.article_card .content h2{
  padding-top: 15px;
  line-height: 1.2;
  text-align: center;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.col_articleslide a, .col_gslide a{
  position: absolute;
    height: 100%;
    width: 5%;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
}
.col_articleslide a[id="blogs-p-btn"], .col_gslide a[id="g-p-btn"]{
  right: 0;
}
.col_articleslide a[id="blogs-n-btn"], .col_gslide a[id="g-n-btn"]{
  left: 0;
}
.col_articleslide a svg, .col_gslide a svg {
  font-size: 40px;
  fill: #5d5d5d;
  cursor: pointer;
}
.article_card .content a{
  text-decoration: none;
  color: #fff;
  background-image: linear-gradient(to bottom , #2b4b98, #356aae);
  padding: 8px;
  padding-left: 24px;
  padding-right: 24px;
  font-size: 14px;
  font-weight: 500;
  border-radius: 20px;
  margin: auto;
  display: table;
  margin-top: 15px;
  margin-bottom: 30px;
  position: initial;
    height: auto;
    width: auto;
}

.col_gslide .g-container a{
  width: 99%;
    display: flex;
    justify-content: end;
    font-size: 1.4vw;
    text-decoration: none;
    color: #356aae;
  position: initial;
  margin-bottom: 40px;
}

.section.gslide{
  background: #f4f7fe;
  margin-top: 40px;
}

.col_fin .fein_cont{
  flex-wrap: wrap;
    display: flex;
    justify-content: center;
}
.col_fin .fein_cont a{
  text-decoration: none;
  color: #000000;
  font-weight: 400;
  border: 1px solid #3d3d3d;
  padding: 1%;
  font-size: 1vw;
  border-radius: 2vw;
  padding-right: 2%;
  padding-left: 2%;
  margin-right: 1.5%;
  margin-bottom: 2%;
}

ol.breadcrumb {
  display: inline-flex;
  gap: 10px;
  list-style: "/";
  margin-top: 150px;
  margin-bottom: 40px;
}
ol.breadcrumb li {
    font-weight: 500;
}
ol.breadcrumb li:nth-child(1) {
  list-style: none;
}
ol.breadcrumb li a {
  color: #000;
}


.single-side img {
  width: 100%;
}
 .single-side h2 {
  margin-top: 13.5%;
    margin-bottom: 4%;
    font-size: 1.2vw;
}
 .single-side p {
    font-size: 1vw;
    line-height: 2vw;
    font-weight: 400;
}

.single-side .one-pop {
  display: flex;
  margin-bottom: 12%;
}
 .single-side .one-pop img {
  width: 32%;
    aspect-ratio: 1/1;
    object-fit: cover;
}
 .single-side .one-pop .content {
  width: 68%;
    padding: 3% 6.5%;
    display: flex;
    flex-direction: column;
}
 .single-side .one-pop .content h3 {
  line-height: 1vw;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    min-height: 2vw;
}
 .single-side .one-pop .content h3 a {
  text-decoration: none;
    color: #000;
    font-size: 1vw;
}
 .single-side .one-pop .content span {
  font-size: 0.8vw;
    color: #333;
    margin-top: 11%;
}
.single-side .follow-cont {
  display: flex;
  justify-content: space-between;
}
 .single-side .follow-cont .follow {
  width: 49%;
    border: 1px solid;
    display: flex;
    justify-content: center;
    align-items: center;
}
 .single-side .follow-cont .follow .content {
  padding: 8%;
}
 .single-side .follow-cont .follow svg {
  font-size: 2vw;
  fill: #60c2f1;
}
.single-side .follow-cont .follow:nth-child(2) svg {
  fill: #3375b0;
}