
.col_catpage .cat-article ul {
    justify-content: end;
      display: flex;
      width: 100%;
      list-style: none;
      gap: 10px;
      border-bottom: 1px solid #333;
  }
  .col_catpage .cat-article ul[id="bott"] {
      border-top: 1px solid #333;
      border-bottom: none;
  }
  .col_catpage .cat-article li {
    padding: 10px;
  }
  .col_catpage .cat-article li a {
    color: #3765f4;
    fill: #3765f4;
  }
  .col_catpage .cat-article.nohover:hover {
    box-shadow: none;
  }
  .col_catpage .cat-article.next_prev {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .col_catpage .cat-article.next_prev a {
    font-weight: 400;
      gap: 20px;
      display: flex;
      flex-direction: column;
      margin-right: 5%;
  }
  .col_catpage .cat-article.next_prev a[id="nxt_prev"] {
    text-align: right;
    margin-right: 0;
      margin-left: 5%;
  }
  .col_catpage .cat-article.next_prev a span {
    font-weight: 600;
  }