.col_catpage{
    display: flex;
    justify-content: space-between;
}
.col_catpage .w70{
    flex-wrap: wrap;
    display: flex;
}

.col_catpage .cat-article {
    display: flex;
      padding: 3%;
      background: #fff;
      padding-bottom: 6.25%;
      margin-bottom: 4%;
      flex-direction: column;
  }
  .col_catpage .cat-article.cat_half {
    width: 48%;
    margin-right: 2%;
  }
  .col_catpage .cat-article:hover {
    box-shadow: 6px 5px 10px -5px;
  }
  .col_catpage .cat-article span {
    width: fit-content;
    border-radius: 5px;
    font-size: 1.25vw;
    color: #333;
    margin-bottom: 4%;
  }
  .col_catpage .cat-article span[id="cat-title"] {
    border: 1px solid #333;
    padding: 0.5% 1.5%;
  }
  .col_catpage .cat-article h2 {
    font-size: 32px;
    line-height: 1.1;
    margin-bottom: 3%;
  }
  .col_catpage .cat-article h2 a {
    text-decoration: none;
      color: #000;
  }
  .col_catpage .cat-article a img {
    width: 100%;
  }
  .col_catpage .cat-article p {
    line-height: 1.6;
    font-size: 16px;
    margin: 15px 0;
  }
  .col_catpage .cat-article a {
    color: #000;
      font-weight: 600;
      text-decoration: none;
  }