.abt_2 {
    margin-top: 100px;
}
.abt_3, .abt_4, .abt_5 {
    justify-content: center;
}
.abt_3, .abt_4 {
    margin-top: 50px;
}
.abt_5 {
    position: relative;
}
.abt_3 p {
    font-size: 20px;
    line-height: 1.6;
    font-weight: 400;
    padding: 0 20px;
    border-left: 2px solid #3a6cf6;
    max-width: 60%;
}
.abt_3 p b {
    width: 100%;
    display: block;
    margin-bottom: 20px;
}
.abt_4 p {
    font-size: 14px;
    line-height: 1.6;
    font-weight: 400;
    max-width: 60%;
}
.abt_4 img {
    width: 20%;
    right: 9%;
    position: absolute;
}

.abt_5 p {
    font-size: 16px;
    line-height: 1.6;
    font-weight: 400;
    padding: 20px;
    border-top: 2px solid #3a6cf6;
    border-bottom: 2px solid #3a6cf6;
    max-width: 60%;
    margin: 50px 0;
}
.abt_5 img{
    position: absolute;
    top: 15px;
    left: 27%;
    width: 85px;
  }
  .abt_5 img[id="bott"]{
    bottom: 15px;
    right: 27%;
    top: auto;
    left: auto;
    transform: rotate(180deg);
  }