.mt1{
    margin-top: 40px;
}

.section_2{
    background: #f4f7fe;
  }
  
  .col_2 span, .col_gslide span, .col_articleslide span, .col_fin span {
    border-bottom: 4px solid #356aae;
      padding-bottom: 2px;
      font-size: 26px;
      width: fit-content;
      display: flex;
      margin: auto;
      margin-bottom: 25px;
  }
  .col_2 img{
      width: 100%;
  }
  
  .col_3 span{
    font-weight: 600;
    color: #356aae;
    letter-spacing: 2px;
    font-size: 32px;
  }
  .col_3 p{
      font-size: 16px;
      margin: 4% 0;
      line-height: 1.6;
      font-weight: 400;
  }
  
  .col_3 a{
    text-decoration: none;
      border: 2px solid #356aae;
      font-size: 1.2vw;
      padding: 0.75vw;
      border-radius: 2vw;
      color: #356aae;
      padding-left: 3.5vw;
      padding-right: 3.5vw;
      position: relative;
  }
  .col_3 a{
    text-decoration: none;
    border: 2px solid #356aae;
    font-size: 18px;
    padding: 12px 24px;
    border-radius: 25px;
    color: #356aae;
    position: relative;
    padding-right: 50px;
    transition: all 0.5s;
  }

  .col_3 a svg{
    position: absolute;
    right: 3px;
    background-image: linear-gradient(to bottom , #2b4b98, #356aae);
    border-radius: 50%;
    height: 40px;
    top: 3px;
    padding: 10px;
    fill: #fff;
    transition: all 0.5s;
  }

  .col_3 a:hover{
    border: 2px solid transparent;
  }
  .col_3 a:hover svg{
      animation: lightSweep 2s infinite;
  }